import React, { useState, useEffect } from 'react';
import ListGroup from 'react-bootstrap/ListGroup';
import Button from 'react-bootstrap/Button';
import {approx} from './utils';
/*
All from https://sensor-js.xyz/demo.html
*/
const TremorApp = ({x_history}) => {

  useEffect(() => {
    window.google.charts.load('current', {packages: ['corechart', 'line']});
  });

  const drawBasic = (chart_id, col, rows) => {
    var data = new window.google.visualization.DataTable();
    data.addColumn('number', 'Time');
    data.addColumn('number', col);

    data.addRows(rows);

    var options = {
      hAxis: {title: 'Time (ms)'},
      vAxis: {title: col}};
    var chart = new window.google.visualization.LineChart(document.getElementById(chart_id));
    chart.draw(data, options);
  }

  return (
    <>
      <div id="base_chart"></div>
      <Button variant="secondary" onClick={() => drawBasic('base_chart', 'X', window.graphs)}>Chart me</Button>
    </>    
  );
}
export default TremorApp;