import React from 'react';
import Navbar from 'react-bootstrap/Navbar';
import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';
import OrientationDisplay from '../components/orientation/OrientationDisplay';
import OrientationTest from '../components/orientation/OrientationTest';
import TremorApp from './TremorApp';
import {isTest} from './utils';

class TremorPage extends React.Component {
  constructor (props) {
    super(props);
    this.state = {
      orientationEvent:null,
      motionEvent:null,
      canPlay:isTest(),
      showOrientationDisplayComponent:true,
      recording:false,
      recordingStartTime:null,
      recordingLength:4000,
      showStuff:null,
      x_history:[],
      y_history:[],
    }
  }
  
  componentDidMount(){
    window.addEventListener("deviceorientation", this.handleOrientation.bind(this));
    window.addEventListener("devicemotion", this.handleMotion.bind(this));
  }
  componentWillUnmount(){
    window.removeEventListener("deviceorientation", this.handleOrientation.bind(this));
    window.removeEventListener("devicemotion", this.handleMotion.bind(this));
  }

  handleOrientation = (event) => {
    this.setState({orientationEvent:event,canPlay:true,});
  }
  
  handleMotion = (event) => {
    //do motion handkle if recording, stop recording if greater than 5 seconds
    if (this.state.recording) {
      let timeFromStart = Date.now()-this.state.recordingStartTime;
      if (timeFromStart > this.state.recordingLength) {this.setState({recording:false});} else {
        window.graphs.push([timeFromStart,event.acceleration.x]);
        this.setState({
          x_history:this.state.x_history.push(event.acceleration.x),
          y_history:this.state.y_history.push(event.acceleration.y),
          showStuff:timeFromStart
        });
      }
    } else {
      this.setState({motionEvent:event,canPlay:true,});
    }
  }

  startRecording = () => {
    window.graphs=[];
    this.setState({recording:true, recordingStartTime:Date.now(), x_history:[], y_history:[]})
  }

  buttons = () => {
    let result = "";
    if (this.state.canPlay){
      if (!this.state.recording){
        result = <Button variant="primary" onClick={() => this.startRecording()}>Start</Button>;
      }
    }
    return result;
  }

  render(){
    return (
      <>
        <Navbar bg="light" expand="lg">
          <Navbar.Brand>Tremor check</Navbar.Brand>
        </Navbar>
        <Card>
          <Card.Body>
            {this.state.canPlay ? this.buttons() : ""} 
            {this.state.canPlay ? <TremorApp eventMo={this.state.x_history} /> : ""} 
            <p>
              <br />
              <OrientationTest canPlay={this.state.canPlay} handleOrientation={this.handleOrientation.bind(this)} />
              {this.state.showOrientationDisplayComponent && !isTest() ? <OrientationDisplay eventOr={this.state.orientationEvent} eventMo={this.state.motionEvent} />:""} 
            </p>
          </Card.Body>
        </Card>
      </>
    )
  }
}
export default TremorPage;